export const validateIndex = (index, options) => {
  if (index < 0) return 0;
  if (index >= options.length) return options.length - 1;
  return index;
};

export const getOptionIndex = (
  options,
  selectedOption,
  valueProperty = 'value',
) => {
  const index = options.findIndex(
    option => option[valueProperty] === selectedOption,
  );
  return validateIndex(index, options);
};

export const isAM = time => time && time.hour() < 12;
