/* eslint-disable max-lines */
import React, { Component } from 'react';
import { findIndex } from 'lodash';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import memoizeOne from 'memoize-one';
import ChevronSmallUp from '@elliemae/ds-icons/ChevronSmallUp';
import DSButton from '@elliemae/ds-basic/Button';
import ChevronSmallDown from '@elliemae/ds-icons/ChevronSmallDown';
import { MoveElementOnScroll } from './moveElementOnScroll';
import { validateIndex } from './utils';

const blockName = 'time-picker-panel';

const Container = aggregatedClasses('div')(
  blockName,
  null,
  ({ scrolling }) => ({ scrolling }),
);
const Wrapper = aggregatedClasses('div')(blockName, 'wrapper');
const PickerItemListContainer = aggregatedClasses('div')(
  blockName,
  'item-list-container',
);
const PickerItemList = aggregatedClasses('div')(blockName, 'item-list');
const PickerHeader = aggregatedClasses('div')(blockName, 'header');
const PickerItemContainer = aggregatedClasses('div')(
  blockName,
  'item',
  ({ selected }) => ({ selected }),
);
const PickerSelectedOptionSelected = aggregatedClasses('div')(
  blockName,
  'selected-container',
);
const SelectedCircle = aggregatedClasses('div')(blockName, 'selected-circle');
const ArrowButtonContainer = aggregatedClasses('div')(
  blockName,
  'arrow-container',
);

const getOptionIndex = (options, selectedOption, valueProperty = 'value') => {
  const index = options.findIndex(
    option => option[valueProperty] === selectedOption,
  );
  return validateIndex(index, options);
};

const arrowProps = {
  buttonType: 'text',
  fluidHeight: true,
  fluidWidth: true,
  size: 's',
};

export default class PickerPanel extends Component {
  computeContainerTranslation = memoizeOne(index => {
    const { options, valueProperty } = this.props;
    const { selectedOption, scrolling } = this.state;

    const selectedOptionIndex =
      index || getOptionIndex(options, selectedOption, valueProperty);
    const listYPosition =
      this.minYListPosition - selectedOptionIndex * this.optionHeight;

    if (this.scrollHandler && !scrolling)
      this.scrollHandler.updatePosition(listYPosition);

    return listYPosition;
  });

  static defaultProps = {
    valueProperty: 'value',
    labelProperty: 'label',
    numberItemsOnScreen: 5, // this number should be impair
    options: [],
    arrowStep: 5,
  };

  constructor(props) {
    super(props);
    this.pickerListContainerRef = React.createRef();
    this.pickerListRef = React.createRef();
    this.pickerItemRef = React.createRef();
    this.selectedOptionRef = React.createRef();

    this.state = {
      selectedOption: props.selected,
      activeIndex: 0,
      // eslint-disable-next-line react/no-unused-state
      prevProps: {},
    };
  }

  componentDidMount() {
    this.computeSizes();
    const pickerListContainerEl = this.pickerListContainerRef.current;
    const pickerListEl = this.pickerListRef.current;

    this.scrollHandler = new MoveElementOnScroll(pickerListEl, {
      container: pickerListContainerEl,
      min: -this.minYListPosition,
      max: this.maxYListPosition,
      onUpdate: (pos, delta) => {
        const { options } = this.props;
        const index =
          options.length -
          -Math.floor((pos - this.maxYListPosition) / this.optionHeight) +
          (delta > 0 ? 1 : -1);
        this.moveTo(index, true);
      },
      onEndScroll: () => {
        this.setState({ scrolling: false });
      },
    });
    this.computeContainerTranslation();
  }

  componentWillUnmount() {
    this.scrollHandler.unsubscribe();
  }

  static getDerivedStateFromProps(nextProps, { prevProps }) {
    const { selected, valueProperty, options } = nextProps;
    if (selected !== prevProps.selected) {
      return {
        selectedOption: selected,
        activeIndex: getOptionIndex(options, selected, valueProperty),
        prevProps: nextProps,
      };
    }
    return null;
  }

  getOptionValue(option) {
    const { valueProperty } = this.props;
    return option[valueProperty];
  }

  getOptionLabel(option) {
    const { labelProperty } = this.props;
    return option[labelProperty];
  }

  moveTo(index, scrolling) {
    const { options } = this.props;
    const { selectedOption } = this.state;
    const validIndex = validateIndex(index, options);
    const option = options[validIndex];
    const optionValue = this.getOptionValue(option);
    if (selectedOption !== optionValue) {
      this.handleOptionSelect(optionValue, validIndex, scrolling);
    }
  }

  computeSizes() {
    const { options, numberItemsOnScreen } = this.props;
    const { offsetHeight: optionHeight } = this.pickerItemRef.current;

    this.optionHeight = optionHeight;
    this.listHeight = this.optionHeight * numberItemsOnScreen;

    this.minYListPosition = this.listHeight / 2 - this.optionHeight / 2;
    this.maxYListPosition =
      this.optionHeight * (options.length - Math.ceil(numberItemsOnScreen / 2));
  }

  handleOptionSelect(optionValue, index, scrolling) {
    const { onChange } = this.props;
    this.setState(
      state => ({
        ...state,
        scrolling,
        selectedOption: optionValue,
        activeIndex: index,
      }),
      () => {
        onChange(optionValue, index);
      },
    );
  }

  // TODO: this needs a refactor from de FocusGroup
  // eslint-disable-next-line max-statements
  handleKeyDown(e) {
    const { activeIndex } = this.state;
    const { options } = this.props;
    const [firstColumn, ...restColumns] = document.getElementsByClassName(
      'em-ds-time-picker-panel',
    );
    const [lastColumn] = restColumns.slice(-1);
    const [prev] = document.getElementsByClassName(
      'DayPickerNavigation_button',
    );
    const focusedElement = document.activeElement;

    switch (e.key) {
      case 'Home':
        this.moveTo(0);
        break;
      case 'End':
        this.moveTo(options.length - 1);
        break;
      case 'ArrowUp':
        this.moveTo(activeIndex - 1);
        break;
      case 'ArrowDown':
        this.moveTo(activeIndex + 1);
        break;
      case 'Tab': {
        e.preventDefault();
        // If it is DateTimePicker, move focus to prev arrow element
        if (prev && focusedElement === lastColumn) {
          prev.focus();
        } else if (focusedElement === lastColumn) {
          firstColumn.focus();
        } else {
          const position = findIndex(restColumns, focusedElement);
          restColumns[position + 1].focus();
        }
        break;
      }
      default:
        break;
    }
  }

  handleStepMoveUp() {
    const { activeIndex } = this.state;
    const { arrowStep } = this.props;
    const supposedPos = activeIndex - arrowStep;
    const nextPos = supposedPos >= 0 ? supposedPos : activeIndex - 1;
    this.moveTo(nextPos);
  }

  handleStepMoveDown() {
    const { activeIndex } = this.state;
    const { arrowStep, options } = this.props;
    const supposedPos = activeIndex + arrowStep;
    const nextPos =
      supposedPos <= options.length ? supposedPos : activeIndex + 1;
    this.moveTo(nextPos);
  }

  renderHeader = () => {
    const { header } = this.props;

    return <PickerHeader>{header}</PickerHeader>;
  };

  render() {
    const { options, hasHeader } = this.props;
    const { selectedOption, activeIndex, scrolling } = this.state;

    const listYPosition = this.computeContainerTranslation(activeIndex);

    return (
      <Wrapper>
        {hasHeader && this.renderHeader()}
        <Container
          classProps={{ scrolling }}
          onKeyDown={this.handleKeyDown}
          tabIndex={0}
        >
          <PickerSelectedOptionSelected innerRef={this.selectedOptionRef}>
            <SelectedCircle />
          </PickerSelectedOptionSelected>
          <ArrowButtonContainer>
            <DSButton
              {...arrowProps}
              aria-label="Chevron Up"
              disabled={activeIndex <= 0}
              icon={<ChevronSmallUp />}
              onClick={this.handleStepMoveUp}
              tabIndex={-1}
            />
          </ArrowButtonContainer>
          <PickerItemListContainer
            innerRef={this.pickerListContainerRef}
            style={{
              height: this.listHeight,
            }}
          >
            <PickerItemList
              innerRef={this.pickerListRef}
              style={{
                transform: `translateY(${listYPosition}px)`,
              }}
            >
              {options.map((option, index) => {
                const value = this.getOptionValue(option);
                const label = this.getOptionLabel(option);
                return (
                  <PickerItemContainer
                    key={value}
                    classProps={{
                      selected: selectedOption
                        ? value === selectedOption
                        : index === 0,
                    }}
                    innerRef={this.pickerItemRef}
                    onClick={() => this.handleOptionSelect(value, index)}
                  >
                    {label}
                  </PickerItemContainer>
                );
              })}
            </PickerItemList>
          </PickerItemListContainer>
          <ArrowButtonContainer>
            <DSButton
              {...arrowProps}
              aria-label="Chevron Down"
              disabled={activeIndex >= options.length}
              icon={<ChevronSmallDown />}
              onClick={this.handleStepMoveDown}
              tabIndex={-1}
            />
          </ArrowButtonContainer>
        </Container>
      </Wrapper>
    );
  }
}
