import React from 'react';
import PropTypes from 'prop-types';
import { DSIconColors, DSIconSizes } from '@elliemae/ds-basic/Icon';
import DatePicker from '@elliemae/ds-icons/DatePicker';
import DSButton, { buttonTypes } from '@elliemae/ds-basic/Button';
import { iconSizes } from '../../../../utils/prop-types';

const Picker = ({
  className = 'date-picker-icon',
  disabled = false,
  icon = <DatePicker />,
  size = DSIconSizes.M,
  buttonActionType = 'secondary',
  onClick = () => null,
  onBlur = () => null,
  readOnly = false,
  ...rest
}) => (
  <DSButton
    aria-label={rest['aria-label'] || 'Date Picker Button'}
    buttonType={buttonActionType}
    className={className}
    color={!disabled ? DSIconColors.PRIMARY : DSIconColors.NEUTRAL}
    disabled={disabled}
    icon={icon}
    onBlur={onBlur}
    onClick={onClick}
    readOnly={readOnly}
    size={size}
  />
);

Picker.propTypes = {
  className: PropTypes.string,
  disabled: PropTypes.bool,
  icon: PropTypes.element,
  size: PropTypes.oneOf(iconSizes),
  buttonActionType: PropTypes.oneOf(buttonTypes),
  onClick: PropTypes.func,
  onFocus: PropTypes.func,
  onBlur: PropTypes.func,
  readOnly: PropTypes.bool,
};
export default Picker;
